// 路由懒加载
const Category = () => import('../components/Category.vue')
const Find = () => import('../components/Find.vue')
const Cart = () => import('../components/Cart.vue')
// const GoodsDetail = () => import( '../components/GoodsDetail.vue'
const Mine = () => import('../components/Mine.vue')
const Login = () => import('../components/Login.vue')
const Reg = () => import('../components/Reg.vue')
const LoginPwd = () => import('../components/LoginPwd.vue')
const Seckill = () => import('../components/seckill/Seckill.vue')
const Detail = () => import('../components/seckill/Detail.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  }, {
    path: '*',
    redirect: '/home'
  }, {
    path: '/search',
    component: SearchPage
  }, {
    path: '/home',
    component: Home
  }, {
    path: '/catgory',
    component: Category
  }, {
    path: '/find',
    component: Find
  }, {
    path: '/cart',
    component: Cart
  }, {
    path: '/search',
    component: SearchPage
  }, {
    path: '/mine',
    component: Mine
  }, {
    path: '/login',
    component: Login
  }, {
    path: '/register',
    component: Reg
  }, {
    path: '/loginPwd',
    component: LoginPwd
  }, {
    path: '/catgory/:id',
    component: Category
  }, {
    path: '/seckill',
    component: Seckill
  }, {
    path: '/detail',
    name: 'Detail',
    component: Detail
  }
]

const router = new VueRouter({
  // mode: 'history',
  scorllBehavior: () => ({
    y: 0

  }),
  routes
})

export default router
