import Vue from 'vue'
import App from './App.vue'
import router from './router'
// 后加入的
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import { Checkbox, Icon, SubmitBar, Search, Sidebar, SidebarItem, CountDown, CheckboxGroup, Stepper, Dialog, Form, TabbarItem, Tabbar, Lazyload, Swipe, SwipeItem } from 'vant'
// import Less from 'Less'
import axios from 'axios'
import store from './store'
// import VueLazyload from 'vue-lazyload'
import Loading from './components/loading'
import http from './api/http'
import './assets/css/base.css'

// Vue.use(http)
Vue.prototype.$axios = axios
Vue.prototype.$http = http
// vant组件
Vue.use(Checkbox, CheckboxGroup, Stepper)
Vue.use(Stepper)
Vue.use(Dialog)
Vue.use(Form)
Vue.use(Tabbar).use(SubmitBar).use(Search).use(Icon).use(CountDown).use(SidebarItem).use(Sidebar).use(TabbarItem).use(Swipe).use(SwipeItem)

Vue.use(ElementUI)
Vue.use(Loading)
Vue.use(Lazyload, {
  preLoad: 1.3, // 预加载高度比例
  error: require('./assets/images/err.png'), // 图片路径错误时加载图片
  loading: require('./assets/images/loading.gif'), // 预加载图片
  attempt: 1, // 尝试加载图片数量
  listenEvents: ['scroll'] // 想要监听的vue事件，默认['scroll']可以省略，当插件跟页面中的动画 或过渡等事件有冲突时，可以尝试其他选项
})

// console.log(router, 11111111111111222222222222222)
// axios.defaults.baseURL = (process.env.NODE_ENV !=='production' ? config.dev.httpUrl:config.build.httpUrl);

// 处理刷新的时候vuex被清空但是用户已经登录的情况
// if (window.sessionStorage.userInfo) {
//   store.dispatch('setUserInfo', JSON.parse(window.sessionStorage.userInfo))
// }

// 需要登录页面验证
router.beforeEach(function (to, from, next) {
  const nextRoute = ['/cart', '/mine', '/course']
  //  是否进入了需要登录的页面
  if (nextRoute.indexOf(to.fullPath) >= 0) {
    if (localStorage.getItem('token')) {
      next()
    } else {
      next({
        path: '/login'
        // query: { redirect: to.fullPath }
      })
    }
  } else {
    next()
  }
  // //已登录的情况再去登录页，跳转至首页
  // if (to.name === 'login') {
  //   if (auth.IsLogin) {
  //     vueRouter.push({ name: 'home' })
  //   }
  // }
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
