  data () {
    return {
      time: 300 * 30 * 60 * 60 * 1000,
      homeDatas: [
        { img_url: require('../assets/images/buy-pro1.jpg'), product_price: 200, product_uprice: 100 },
        { img_url: require('../assets/images/none.jpg'), product_price: 200, product_uprice: 100 },
        { img_url: require('../assets/images/biubiu.jpg'), product_price: 200, product_uprice: 100 },
        { img_url: require('../assets/images/test.jpg'), product_price: 200, product_uprice: 100 },
        { img_url: require('../assets/images/e1fd61f13c6b5910.jpg'), product_price: 200, product_uprice: 100 }
      ],
      mainDatas: [
        { img_url: require('../assets/images/buy-pro1.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/none.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/biubiu.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/test.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/buy-pro1.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/none.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/biubiu.jpg'), product_name: '每日特价', product_price: 100 },
        { img_url: require('../assets/images/test.jpg'), product_name: '每日特价', product_price: 100 }
      ],
      day: 0,
      hr: 0,
      min: 0,
      sec: 0

    }
  },
  mounted () {
    // this.getData()
    this.countdown()
  },
  methods: {
    countdown () {
      // 定义结束时间戳
      const end = Date.parse(new Date('2021-1-18 0:00:00'))
      // 定义当前时间戳
      const now = Date.parse(new Date())
      // 做判断当倒计时结束时都为0
      if (now >= end) {
        this.day = 0
        this.hr = 0
        this.min = 0
        this.sec = 0
        return
      }
      // 用结束时间减去当前时间获得倒计时时间戳
      const msec = end - now
      const day = parseInt(msec / 1000 / 60 / 60 / 24) // 算出天数
      const hr = parseInt(msec / 1000 / 60 / 60 % 24)// 算出小时数
      const min = parseInt(msec / 1000 / 60 % 60)// 算出分钟数
      const sec = parseInt(msec / 1000 % 60)// 算出秒数
      // 给数据赋值
      this.day = day
      this.hr = hr > 9 ? hr : '0' + hr
      this.min = min > 9 ? min : '0' + min
      this.sec = sec > 9 ? sec : '0' + sec
      // 定义this指向
      const that = this
      // 使用定时器 然后使用递归 让每一次函数能调用自己达到倒计时效果
      setTimeout(function () {
        that.countdown()
      }, 1000)
    }
    // getData () {
